<template>
  <div>
    <ul id="business">
      <li>
        <h2 class="title"></h2>
        <p class="title_eng">Development services</p>
        <p class="list">
           <br>
          <br>
          <br>
          <br>
          <br>
          
        </p>
        <button @click="goContact"></button>
      </li>
      <li>
        <h2 class="title"></h2>
        <p class="title_eng">Interface design</p>
        <p class="list">
          11<br>
          <br>
          <br>
          app<br>
          <br>
          <br>
        </p>
        <button @click="goContact"></button>
      </li>
      <li>
        <h2 class="title"></h2>
        <p class="title_eng">The plane design</p>
        <p class="list">
          LOGO<br>
          VI<br>
          <br>
          <br>
          <br>
          <br>
        </p>
        <button @click="goContact"></button>
      </li>
    </ul>
  </div>

</template>

<script>
  export default {
    name: 'business',
    data () {
      return {}
    },
    methods:{
      goContact(){
        this.$router.push('/contact')
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import "../../common/css/mixins";

  #business {
    padding: 0.44rem;
    color: #fff;
  }

  li {
    position: relative;
    width: 100%;
    height: 6.5rem;
    margin-bottom: 0.44rem;
    box-sizing: border-box;
    padding: 0.7rem 0.5rem;
    border-radius: 12px;
    &:nth-child(1) {
      background: url("./bg1.png") center no-repeat;
      background-size: 100%;
    }
    &:nth-child(2) {
      background: url("./bg2.png") center no-repeat;
      background-size: 100%;
    }
    &:nth-child(3) {
      background: url("./bg3.png") center no-repeat;
      background-size: 100%;
    }
    .title {
      font-size: 0.7rem;
    }
    .title_eng {
      margin-bottom: 0.6rem;
    }
    .list {
      line-height: 0.6rem;
    }
    button {
      width: 1.34rem;
      height: 0.54rem;
      background: #ffffff;
      position: absolute;
      bottom: 0.7rem;
      right: 0.5rem;
      color: $themeColor;
      border-radius: 0.27rem;
    }
  }
</style>



